@import "../../Common/commonStyle.less";

.nav-pc {
    width: 100%;
    background: linear-gradient(270deg, rgba(100, 223, 222, 0) 0%, rgba(100, 223, 222, 0.2) 47%, rgba(100, 223, 222, 0) 100%);
    border-bottom: 1px solid;
    border-image-source: linear-gradient(270deg, rgba(100, 223, 222, 0.8), rgba(100, 223, 222, 0), rgba(100, 223, 222, 1));
    border-image-slice: 0 0 100% 0;
    border-image-width: 1px;

    .upload-box {
        display: inline-block;
        margin-left: 10%;

        div {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            color: #fff;
            padding: 9.5px 0;
            cursor: pointer;
        }
    }

    .logo-info {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-left: 10px;

        .loginBtn {
            color: @fontColor;
        }

        .switch-language-box {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            .ant-select-arrow {
                color: #fff !important;
            }
        }
    }
}

@media screen and(max-width: 1440px) {
    .nav-pc {
        .nav-row {
            height: 45px;

            .upload-box {
                .logoImg {
                    width: 24px;
                    margin-right: 20px;
                }

                span {
                    font-size: 1rem;
                    font-family: @PingFang-SC-Bold-fontFamily;
                    font-weight: bold;
                    color: rgba(255, 255, 255, 1);
                    line-height: 20px;
                    text-shadow: 0px 3px 1rem rgba(100, 223, 222, 0.5);
                }
            }

            .loginBtn {
                font-size: 1rem;
                margin-left: 10px;
                line-height: 20px;
                text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.5);
            }
        }
    }

}

@media screen and(min-width: 1441px) {
    .nav-pc {
        .nav-row {
            height: 60px;

            .upload-box {
                .logoImg {
                    width: 32px;
                    margin-right: 20px;
                }
                span {
                    font-size: 1rem;
                    font-family: @PingFang-SC-Bold-fontFamily;
                    font-weight: bold;
                    color: rgba(255, 255, 255, 1);
                    line-height: 27px;
                    text-shadow: 0px 3px 1rem rgba(100, 223, 222, 0.5);
                }
            }

            .loginBtn {
                font-size: 1rem;
                margin-left: 10px;
                text-shadow: 0px 3px 19px rgba(100, 223, 222, 0.5);
            }
        }
    }

}
