    border-image-slice: 0 0 100% 0;
    border-image-width: 1px;

    .upload-box {
        display: inline-block;
        margin-left: 10px;

        div {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            color: #fff;
            padding: 9.5px 0;
            cursor: pointer;

            .logoImg {
                width: 24px;
                height: 24px;
