@import "../../Common/commonStyle.less";

.phone-footer {

    padding-bottom: 2rem;

    p,
    ul {
        margin: 0;
        padding: 0;
    }

    text-align: center;

    background:linear-gradient(270deg, rgba(100, 223, 222, 0.04) 0%, rgba(100, 223, 222, 0.1) 47%, rgba(100, 223, 222, 0.04) 100%);

    .footer-item {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        padding: 2rem;

        .footer-title {
            font-size: 1.25rem;
            line-height: 1.72rem;
            font-family: @PingFangSC-Regular-fontFamily;
            font-weight: 400;
            color: rgba(100, 223, 222, 1);
            margin-bottom: 10px !important;
        }

        a {
            font-size: 1.17rem;
            line-height: 1.64rem;
            font-family: @PingFangSC-Regular-fontFamily;
            font-weight: 400;
            color: rgba(255, 255, 255, .7);
        }
    }

    .explanationSegmentationImg {
        width: 100%;
        height: 12px;
        margin-bottom: 2rem;
    }

    .record {
        font-size: 1.09rem;
        font-family: @PingFangSC-Light-fontFamily;
        font-weight: 300;
        color: rgba(255, 255, 255, .7);
        line-height: 1.56rem;
        text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
    }
}