@import "../../Common/commonStyle.less";

.homePage-phone {
    p {
        margin: 0;
        padding: 0;
    }

    position: relative;
    width: 100%;
    background-color: rgba(19, 20, 44, 1);

    // 头部样式
    main {
        position: relative;
        width: 100vw;
        height: 100*1.52vw;
        background:
            url('../../Assets/header/phone_01@2x.png') 0 0 no-repeat,
            url('../../Assets/header/phone_02@2x.png') 0 53vw no-repeat,
            url('../../Assets/header/phone_03@2x.png') 0 105.9vw no-repeat;

        background-size: 100%;
        margin-bottom: 2rem;

        hgroup {
            position: relative;
            text-align: center;
            z-index: 3;
            padding-top: 12rem;

            .banner-p1 {
                font-size: 2.97rem;
                line-height: 4.14rem;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                font-family: @PingFang-SC-Heavy-fontFamily;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
                letter-spacing: 1px;
            }

            .banner-p2 {
                font-size: 1.09rem;
                line-height: 1.56rem;
                text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.3);
                font-family: @PingFang-SC-Heavy-fontFamily;
                font-weight: 800;
                color: rgba(100, 223, 222, 1);
            }
        }

        // 联系我们
        .contact-us-txt {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin-top: 20%;

            p {

                font-size: 1.88rem;
                line-height: 2.58rem;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                font-family: @PingFang-SC-Heavy-fontFamily;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
                margin-bottom: 1.7rem;
            }

            .uploadBtn {
                position: relative;
                width: 82vw;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                background: none;
                border: none;
                outline: none;
                margin: 0;
                padding: 0;
                cursor: pointer;

                .btn-bg {
                    width: 100%;

                }

                .btn-icon {
                    position: absolute;
                    width: 50px;
                    left: 16%;
                    margin-right: 10px;
                }

                .btn-txt {
                    position: absolute;
                    left: 32%;
                }
                .btn-txt-google{
                    left: 30%;
                }

                span {
                    font-size: 1.88rem;
                    line-height: 2.58rem;
                    text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                    margin-left: 18px;
                    font-family: @PingFangSC-Regular-fontFamily;
                    font-weight: 400;
                    color: rgba(255, 255, 255, 1);

                }


                .googlePlay {
                    margin-right: 7px;
                }
            }

            .btn1 {
                // margin-top: 1rem;
            }

            .btn2 {
                margin-top: 1.8rem;
            }
        }

    }
}

.phoneEnTxtStyle {
    main {
        hgroup {
            .banner-p1 {
                font-size: 1.7rem;
                line-height: 2.5rem;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
            }

            .banner-p2 {

                font-size: 1.1rem;
                font-family: @PingFang-SC-Medium-fontFamily;
                font-weight: 500;
                color: rgba(100, 223, 222, 1);
                line-height: 1.67rem;
                text-shadow: 0px 0px 1px rgba(100, 223, 222, 0.3);
            }


        }

        .contact-us-txt {
            margin-top: 30%;

            p {

                font-size: 1.67rem;
                line-height: 2.33rem;
                text-shadow: 0px 0px 1px rgba(100, 223, 222, 0.6);
            }
        }
    }
}