@import "../../Common/commonStyle.less";

.language-box {

    ul,
    li {
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    &:hover {
        .lng-icon {
            transform: rotate(180deg);
        }
    }

    position: relative;

    .lng-cnt {
        position: relative;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

    }

    .lngbox {
        position: absolute;
        right: -25%;
        transition: all 10s ease-in;

        .lngList {
            margin-top: 5%;
            width: 120px;
            background-image: url(../../Assets/header/lngbox@2x.png);
            background-size: 100% 100%;
            background-repeat: no-repeat;
            padding: 20px;


            li {
                cursor: pointer;
                font-size: 1rem;
                font-family: @PingFang-SC-Medium-fontFamily;
                font-weight: 500;
                line-height: 20px;
                text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.5);
                text-align: center;
                color: @fontColor;
            }

            li:hover,
            li:active {
                color: #fff;
            }

            li:nth-child(1) {
                background-image: url('../../Assets/home/explanation-segmentation@2x.png');
                background-size: 100%;
                background-repeat: no-repeat;
                background-position-y: bottom;
                padding-bottom: 15px;
            }

            li:nth-child(2) {
                padding-top: 5px;

            }
        }

    }

    .language-txt {
        font-size: 1rem;
        font-family: @PingFang-SC-Bold-fontFamily;
        font-weight: bold;
        color: rgba(255, 255, 255, 1);
        line-height: 20px;
        text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.5);
        padding: 0 10px;

        .lng-icon {
            width: 20px;
            height: 10px;
            transition: all 0.2s ease-in-out;
        }

        .lng-icon:last-of-type {
            fill: #fff;
        }
    }

    .checkClose {
        transition: all 10s ease-in-out;
        display: none;

    }
}


@media screen and(max-width: 1439px) {

    .headerMap {
        width: 24px;
        height: 24px;
    }

    .headerMap:last-of-type {
        fill: @fontColor;
    }

}

@media screen and(min-width: 414px)and(max-width: 1440px) {
    .lngbox {
        right: 0% !important;
        .lngList {
            margin-top: 10% !important;
        }
    }
}
@media screen and(min-width: 1441px) {
    .lngbox {
        right: 0% !important;
        .lngList {
            margin-top: 15% !important;
        }
    }
}

@media (min-width: 1440px)and(max-width: 1920px) {

    .headerMap {
        width: 14px;
        height: 14px;
    }

    .headerMap:last-of-type {
        fill: #fff;
    }
}